import React from 'react';
import { useAuth } from '@clerk/clerk-react';
import { Navigate } from 'react-router-dom';
import './auth.css';

export default function WorldMap() {
  const { isSignedIn, user, signOut, isLoaded } = useAuth();
  
  // Show loading if Clerk is still loading
  if (!isLoaded) {
    return (
      <div className="auth-container">
        <div className="auth-card">
          <div className="loading">Loading...</div>
        </div>
      </div>
    );
  }
  
  // If user is not signed in, redirect to home
  if (!isSignedIn) {
    return <Navigate to="/" replace />;
  }

  return (
    <div className="auth-container">
      <div className="auth-card" style={{ maxWidth: '600px' }}>
        <div className="auth-header">
          <div className="logo">
            <div className="logo-icon"></div>
            <h1>Welcome to Hackout25!</h1>
          </div>
          <p className="auth-subtitle">
            Hello, {user.emailAddresses[0]?.emailAddress}!
            <br />This is where your map content would be displayed.
          </p>
        </div>
        
        <div className="auth-content" style={{ textAlign: 'center' }}>
          <div style={{
            background: '#f0fdf4',
            padding: '20px',
            borderRadius: '12px',
            border: '1px solid #10b981',
            marginBottom: '24px'
          }}>
            <p style={{ color: '#059669', margin: '0', fontWeight: '500' }}>
               Authentication successful! Your map and other features will be implemented here.
            </p>
          </div>
          
          <button 
            onClick={() => signOut()}
            className="clerk-button"
            style={{
              background: 'linear-gradient(135deg, #10b981 0%, #34d399 100%)',
              border: 'none',
              borderRadius: '12px',
              padding: '14px 24px',
              fontWeight: '600',
              fontSize: '16px',
              color: 'white',
              cursor: 'pointer',
              transition: 'all 0.2s ease',
              boxShadow: '0 4px 15px rgba(16, 185, 129, 0.3)'
            }}
            onMouseEnter={(e) => {
              e.target.style.transform = 'translateY(-2px)';
              e.target.style.boxShadow = '0 8px 25px rgba(16, 185, 129, 0.4)';
            }}
            onMouseLeave={(e) => {
              e.target.style.transform = 'translateY(0)';
              e.target.style.boxShadow = '0 4px 15px rgba(16, 185, 129, 0.3)';
            }}
          >
            Sign Out
          </button>
        </div>
        
        <div className="auth-footer">
          <p>&copy; 2025 Hackout25. All rights reserved.</p>
        </div>
      </div>
      
      {/* Background decorative elements */}
      <div className="bg-decoration">
        <div className="circle circle-1"></div>
        <div className="circle circle-2"></div>
        <div className="circle circle-3"></div>
      </div>
    </div>
  );
}
