import React from 'react';
import { useAuth } from '@clerk/clerk-react';
import { SignIn } from '@clerk/clerk-react';
import { Navigate } from 'react-router-dom';
import './auth.css';

export default function AuthPage() {
  const { isSignedIn, isLoaded } = useAuth();

  // If user is already signed in, redirect to map
  if (isLoaded && isSignedIn) {
    return <Navigate to="/map" replace />;
  }

  // Show loading if Clerk is still loading
  if (!isLoaded) {
    return (
      <div className="auth-container">
        <div className="auth-card">
          <div className="loading">Loading...</div>
        </div>
      </div>
    );
  }

  // Show login page directly when app loads
  return (
    <div className="auth-container">
      <div className="auth-card">
        <div className="auth-header">
          <div className="logo">
            <div className="logo-icon">H25</div>
            <h1>Hackout25</h1>
          </div>
          <p className="auth-subtitle">Welcome! Please sign in to continue</p>
        </div>
        
        <div className="auth-content">
          <SignIn 
            afterSignInUrl="/map" 
            signUpUrl="/signup"
            appearance={{
              variables: {
                colorPrimary: '#10b981',
                colorBackground: '#ffffff',
                colorInputBackground: '#f8fafc',
                borderRadius: '12px'
              },
              elements: {
                card: 'clerk-card',
                headerTitle: 'clerk-title',
                formFieldInput: 'clerk-input',
                formButtonPrimary: 'clerk-button'
              }
            }}
          />
        </div>
        
        <div className="auth-footer">
          <p>&copy; 2025 Hackout25. All rights reserved.</p>
        </div>
      </div>
      
      {/* Background decorative elements */}
      <div className="bg-decoration">
        <div className="circle circle-1"></div>
        <div className="circle circle-2"></div>
        <div className="circle circle-3"></div>
      </div>
    </div>
  );
}
